.singleItem {
  flex: 6;

  .top {
    padding: 20px;
    display: flex;
    gap: 20px;

    .left {
      flex: 1;
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 20px;
      position: relative;

      .editButton {
        position: absolute;
        top: 0;
        right: 0;
        padding: 5px;
        font-size: 15px;
        color: #7451f8;
        background-color: #7551f818;
        cursor: pointer;
        border-radius: 0px 0px 0px 5px;
      }

      .title {
        font-size: 20px;
      }

      .item {
        display: flex;
        align-items: center;
        gap: 50px;
        height: max-content;

        .itemImg {
          width: 200px;
          height: 200px;
          border-radius: 50%;
          object-fit: cover;
        }

        .details {
          width: 40%;
          .itemTitle {
            margin-bottom: 10px;
            color: #555;
          }

          .detailItem {
            margin-bottom: 10px;

            .itemKey {
              font-weight: bold;
              color: gray;
              margin-right: 5px;
              font-size: 18px;
            }

            .itemValue {
              font-weight: 300;
              font-size: 18px;
            }
          }
        }
      }
    }

    .right {
      flex: 2;
    }
  }

  .bottom {
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 20px;
    margin: 10px 20px;
  }

  .title {
    font-size: 16px;
    color: lightgray;
    margin-bottom: 20px;
  }
}
